.technology {
    max-width:1100px;
    margin: 0 auto 52px auto;

    &--gallery{
        display: flex;
        margin-top: 67px;
        margin-left: -4px;
    }

    .gallery{

        &__box{
            width: 255px;
            height: 226px;

            &--2{
                margin-left: 30px;
                margin-right:15px;
            }
            &--3{
                margin-left: 15px;
                margin-right:30px;
            }
        }
        
    }

    .box{
        margin: 0 auto;
        position:relative;
        width: 117px;
        height: 120px;
        &__number{
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 100px;
            line-height: 120px;
            text-align: center;
            color: rgba(16, 16, 16, 0.08);
        }

        &--cloud{
            position:relative;
            width:80px;
            height: 80px;
            top:-65px;
            left:16px;

            &--cloud{
                position: absolute;
                left: 6.25%;
                right: 6.25%;
                top: 15.62%;
                bottom: 25.78%;
            }
            &--arrow{
                position: absolute;
                left: 38.28%;
                right: 38.28%;
                top: 44.53%;
                bottom: 15.63%;
            }
        }

        &--secure{
            position:relative;
            top:-57px;
            left: 29px;
            width: 66px;
            height: 66px;
            width: 47.68px;
            height: 59.3px;

            &--icon {
                position: absolute;
            left: 13.87%;
            right: 13.89%;
            top: 5.08%;
            bottom: 5.07%;
            }
        }

        &--pdf {
            position: relative;
            width: 70px;
            height: 70px;
            top: -53px;
            left: 39px;

            &--icon {
                left: 15.84%;
                right: 15.84%;
                top: 11.43%;
                bottom: 11.43%;
                width: 47.82px;
                height: 54px;
            }
        }

        &--csv {
            position: relative;
            width: 47px;
            height: 54px;
            left: 40px;
            top: -51px;

            &--icon {
                position: absolute;
                left: 0%;
                right: 0%;
                top: 0%;
                bottom: 1.85%;
            }
        }
    }

    
    @media only screen and(max-width:1140px){

        &--gallery{
        flex-wrap:wrap; 
        justify-content: center;          
        }

        .gallery__box--2 {
            margin-left:0;
            margin-right:0;
        }
        .gallery__box--3 {
            margin-left:0;
            margin-right:0;
        }
    }
}