.header{
background-image: url('../images/header/background-image-header.jpg');
background-repeat: no-repeat;
width: 100%;
height:100%;
background-size: cover;
position: relative;
padding-top: 17px;
padding-bottom: 187px;
background-position: 50% 50%;

    &:before {
        content: '';
        top: 0;
        left:0;
        width: 100%;
        height:100%;
        position: absolute;
        background: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%);
    }

    &--text {
        position: relative;
        max-width: 540px;
        height: 124px;
        font-family: 'Lato', sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size:24px;
        line-height: 150%;
        text-align: center;
        color: $white;
        margin: 39px auto;
        letter-spacing: 0.2px;
    }

    @media only screen and (max-width: 850px){
      
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 120px;
        &--text {
            font-size:20px;
            line-height: 30px;
            margin: 60px auto 0 auto;
            display:block;
        }
      }

      @media only screen and (max-width: 800px){
      

        &--text {
            font-size:20px;
            line-height: 30px;
            margin: 60px auto 60px auto;
            display:block;
        }
    }


}