.contact{
padding-top: 117px;
margin-left: 176px;
padding-bottom:58px;
display: flex;

    &--left{
        margin-right: 125px;
        max-width: 445px;
    }

    &--right{
    max-width: 540px;
    }


.text--contact{
    max-width: 434px;
    min-height: 64px;
    font-family: Lato;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    text-align: justify;
    color: $black;
    margin-top:55px;
}



    @media only screen and (max-width: 1439px){

        margin-left: 12.222%;


        &--left{
            margin-right: 8.68%;
        }
        
        }

    @media only screen and (max-width: 1300px){
        align-items: center;
        flex-direction: column;
        margin-left:0;

        padding-left: 15px;
        padding-right: 15px; 
            
        
        &--left{
            margin-right:0;
            margin-left:0;
        }

        &--right{
            margin-top: 40px;
        }
       
    }
    @media only screen and (max-width: 627px){
        .margin--flex {
            margin-top: 20px;
        }
    }

      

}
