.rectangle{
    width: 293px;
    height: 198px;
    background: $white;
    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    position: absolute;
    left: 29px;
    top: 156px;

    &__link {
        width: 253px;
        height: 18px;
        font-family: 'Lato', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size:12px;
        line-height: 150%;
        text-align: center;
        margin: 0 auto;
        color: $red;
    }
    .readmore{
        text-align: center;
        margin-top: -3px;
    }

    .font--small{
        width: 229px;
        height: 57px;
        font-family: 'Lato', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size:12px;
        line-height: 22px;
        line-height: 150%;
        color: $black2;
        margin: 0px auto 5px auto;
        text-align: center;
   
    }

    @media only screen and (max-width: 500px){
        margin-left: 0%;
    }

    @media only screen and (max-width: 390px){
        width: 90%;
        margin-left:auto;
        margin-right:auto;
        left:5%;

    }

}