    // Font Smoothing

    body{
    -webkit-font-smoothing : antialiased; // Chrome, Safari
    -moz-osx-font-smoothing: grayscale;   // Firefox
}

//
// Heading styles
//



h1, .h1 {
    position: relative;
    font-size: 72px;
    line-height: 86px;
    font-style: normal;
    color: $white;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
    letter-spacing: 0.056em;
    text-transform: uppercase;
    text-align: center;
    max-width: 578px;
    max-height: 86px;
    display: flex;
    align-items: center;
    margin:140px auto 0 auto;
    
    @media only screen and (max-width: 850px){
        font-size:52px;
        line-height: 66px;
        margin: 80px auto 0 auto;
        display:block;
      }

      @media only screen and (max-width: 500px){
        font-size:32px;
        line-height: 46px;
        margin: 60px auto 0 auto;
      }
}

h2, .h2 {
    position: relative;
    font-family: 'Lato', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size:  36px;
    line-height: 43px;
    text-align: center;
    color: $black;
    letter-spacing: -0.1px;

    @media only screen and (max-width: 850px){
        font-size:28px;
        line-height: 33px;
      }

      &--technology{
          padding-top:  5px;
      }

    &--appDownload {
        margin-left: 0;
        max-width: 445px;
        height: 107px;
        letter-spacing: 0;
        color: $white;
        text-align: left;

        @media only screen and (max-width: 1230px){
            max-width: 1100px;
            height: auto;
            
        }

        @media only screen and (max-width: 850px){
            font-size:28px;
            line-height: 33px;
            
        }
    }
    &--contact {
        letter-spacing: 0;
        margin-left:0;
        text-align: left;
    }
}


h3, .h3 {
    max-width: 229px;
    min-height: 40px;
    font-family: 'Lato', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size:18px;
    line-height: 22px;
    text-align: center;
    color: $black2;
    margin: 17px auto -8px auto;

    &--features{
        margin-top: 34px;
        margin-bottom: 19px;
    }
    }

.font--small{
    font-family: Lato;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    width: 191px;
    height: 51px;
    margin: 0 auto;
}

p {
    font-family: 'Lato', sans-serif;

 
}

html {
    scroll-behavior: smooth;
  }


a {
    text-decoration: none;
    cursor:pointer;
    color: $white;

    &:hover {
        color: $red2;
    }
}

