.name {
    width:255px;
    height:60px;
    border: 1px solid #333333;
    box-sizing: border-box;
    border-radius: 6px;
    padding: 31px;
    font-family: Lato;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: rgba(19, 39, 67, 0.5);
    border-radius: 6px;
}
.fullname{
    display: flex;
    justify-content: space-between;
    width: 540px;
}

.other{
    display: block;
    padding: 31px;
    width: 100%;
    height: 60px;
    border: 1px solid #333333;
    box-sizing: border-box;
    border-radius: 6px;
    font-family: Lato;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: rgba(19, 39, 67, 0.5);
    margin-top: 2px;
}

.margin--phone{
    margin:18px 0;
}

.positive {
    color:green;
}

.negative {
    color: red;
}

    @media only screen and (max-width: 627px){
    .name, .lastname{
        width: 100%;
    }
    @media only screen and (max-width: 627px){
        .fullname{
            flex-wrap:wrap;
           width:100%;
        }
    }
}
