.button {
    position: relative;
    width: 160px;
    height: 50px;
    background-color: $white;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    
    font-family: 'Lato', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size:14px;
    line-height: 17px;
    text-transform: uppercase;
    color: $red;




 
    &--contact{
        margin: 27px 0 20px 0;
        background-color: $red2;
        color: $white;
    }
}