
/* Navbar */
.navbar{
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 100px;
    color: $white;

    &--logo{
        width:172px;
        height: 29px;
        margin: 42px 0 36px 11.5%;
        display: flex;
    

        &--text{
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            margin-left: 9px;
            text-transform: uppercase;
        }

        &--icon{
            width: 24.65px;
            height: 17.4px;
            margin-top:2px;
        }
    }

    &--ul{
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 445px;
        margin-right: 160px;
        margin-top:2px;
    }
    
    &__li{
        text-transform: uppercase;
        font-family: Lato;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        
    }

   
}

@media only screen and (max-width: 850px){
    .navbar{
        &--ul{
            display: none;
        }
    }

}