.container{
    max-width:1440px;
    margin: 0 auto;
 
    &--features {
        max-width: 1440px;
        margin: 0 auto 0 auto;
        background-color: $grey;
        padding-top: 63px;
        padding-bottom: 226px;    /*+105 becouse of position absolute*/
    }

    &--technology {
        max-width: 1440px;
        margin: 61px auto 100px auto;
    }

    &--appDownload {
        max-width: 1440px;
        background-color: $red2;
    }

    @media only screen and(max-width:850px){
        &--features {
            padding-left: 15px;
            padding-right: 15px;
            padding-bottom: 156px;    
        }
    }

    


  
}