.features {

    max-width:1100px;
    margin: 0 auto;

    &--gallery{
        display: flex;
        margin-top: 76px;
        margin-left: -5px;


            &__space{
                margin-left: 30px;
            }

            &__card{
                position: relative;


                &--2{
                    margin-left:30px;
                    margin-right:30px;
                }
    
    
            &__img{
                border-radius: 8px;
                max-width: 350px;
                height: auto;
            }
        }
        }

        @media only screen and (max-width: 1200px){
            &--gallery{
                flex-wrap: wrap;
                justify-content: center;


                &__card{

                    &--2{
                 margin-bottom: 150px;
                    }
                    &--1{
                        margin-bottom: 150px;
                        }
                    
                }
            }
        }

        @media only screen and (max-width: 500px){
            &--gallery{
                max-width:350px;
                margin-left: auto;
                margin-right:auto;

                &__img {
                    width: 100%;
                    height: auto;
                }
                
                &__card{

                    &--2{
                    margin-left:0;
                    margin-right:0;
                    margin-bottom:200px;
                    }
                    &--1{
                        margin-bottom: 200px;
                        }
                    
                }
            }
        }


      
}