.appDownload {
display: flex;


    &--left {
        margin-top: 124px;
        max-width: 445px;
        margin-left: 164px;
        margin-right:81px;

    }

    &--right {
        margin-top: 94px;
        width: 533px;
        height: 311px;
        position: relative;
        margin-bottom: 95px;
    }

.slider{
    &--front {
        position: absolute;
        left: 8.25%;
        max-width: 445px;
        height: auto;
        z-index:2;
    }
    &--left {
        position: absolute;
        width: 383px;
        height: 267px;
        border-radius: 15px;
        top: 22px;
    }
    &--right {
        position: absolute;
        width: 383px;
        height: 267px;
        border-radius: 15px;
        top: 22px;
        right:0;
    }
}


.text--appDownload{
    font-family: Lato;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    /* or 20px */
    max-width: 445px;
    min-height: 64px;
    text-align: justify;
    margin-top:24px;
    margin-bottom: 40px;
    color: $white;
    }
    &--icon{
        width: 134px;
        height:32px;
        display: flex;
    }

    .icon {
        display: flex;
        position: relative;
        top:0;
        width: 32px;
        height: 32px;

        &--1{
            margin-right: 25px;
            &--left{
                position: absolute;
                left: 9.38%;
                right: 49.22%;
                top: 10.78%;
                bottom: 10.78%;
                width:13.15px;
                height:25.1px;
            }

            &--top{
                position: absolute;
                left: 16.15%;
                right: 32.46%;
                top: 6.25%;
                bottom: 54.7%;
                width: 16.44px;
                height: 12.5px;                
            }
            &--bottom{
                width: 16.44px;
                height: 12.5px;  
                position: absolute;
                left: 16.15%;
                right: 32.46%;
                top: 54.7%;
                bottom: 6.25%;
            }
            &--right{
                position: absolute;
                left: 60.62%;
                right: 9.38%;
                top: 37.41%;
                bottom: 37.42%;
                width: 9.6px;
                height: 8.06px;
            }

        }
        &--2{
            margin-right: 25px;
            &--apple{
                position: absolute;
                left: 6.25%;
                right: 6.25%;
                top: 6.25%;
                bottom: 6.25%;
                width: 28px;
                height: 28px;
            }
        }
        &--3{
            top: 4px;
                width: 24px;
                height: 24px;

            &--windows{

                position: absolute;

            }
        }
    }

    
    @media only screen and (max-width: 1439px){
        &--left {
            margin-left: 11.45%;
        }
        

    }
    @media only screen and (max-width: 1230px){

        
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding-left:30px;
        padding-right:30px;

        &--right {
            margin-top: 45px;
            margin-bottom: 60px;
            margin-right: 0;

        }
        &--left {
            margin-top: 60px;
            max-width: 700px ;
            margin-right: 0;
            margin-left:0;
        }
        

        .text--appDownload{
            margin-top:15px;
            margin-bottom: 25px;
            text-align:left;
            }
    }
    @media only screen and (max-width: 570px){
    .slider{
        &--left,&--right{
            display: none;
        }
        &--front {
            margin: 0 auto;
            position: initial;
        }


        
    }
    .img--front{
        width: 100%;
        height: auto;
    }
    .appDownload{
        &--right{
            width:90%;
            height:auto;
        }
    }
    }

}